<template>
    <div>
      <md-progress-bar class="md-accent" md-mode="indeterminate" v-if="loaded"></md-progress-bar>
      <md-tabs class="md-accent" md-alignment="centered" @md-changed="tab">
        <md-tab id="tab-active" md-label="Active">
          <md-table>
            <md-table-row>
              <md-table-head>Trailer ID</md-table-head>
              <md-table-head>Truck ID</md-table-head>
              <md-table-head>City</md-table-head>
              <md-table-head>State</md-table-head>
              <md-table-head>Reserved Time</md-table-head>
              <md-table-head>Reserve Expire Time</md-table-head>
              <md-table-head>Status</md-table-head>
              <md-table-head>Actions</md-table-head>
            </md-table-row>
            <md-table-row v-for="ac in activeRes" :key="ac">
              <md-table-cell>{{ ac.trailerid }}</md-table-cell>
              <md-table-cell>{{ ac.truckid }}</md-table-cell>
              <md-table-cell>{{ ac.city }}</md-table-cell>
              <md-table-cell>{{ ac.state }}</md-table-cell>
              <md-table-cell>{{ ac.reserved }}</md-table-cell>
              <md-table-cell>{{ ac.time }}</md-table-cell>
              <md-table-cell>{{ ac.status }}</md-table-cell>
              <md-table-cell style="width:15%">
              <md-button class="md-icon-button md-raised" @click="hookup(ac._id,ac.time)">
                <md-icon>rv_hookup</md-icon>
              </md-button>
              <md-button class="md-icon-button md-raised" @click="release(ac._id,ac.trailerid,ac.truckid,ac.time)">
                <md-icon>clear</md-icon>
              </md-button>
              </md-table-cell>
            </md-table-row>
          </md-table>
        </md-tab>
        <md-tab id="tab-hooked" md-label="Hooked">
          <md-table>
            <md-table-row>
              <md-table-head>Trailer ID</md-table-head>
              <md-table-head>Truck ID</md-table-head>
              <md-table-head>City</md-table-head>
              <md-table-head>State</md-table-head>
              <md-table-head>Reserved Time</md-table-head>
              <md-table-head>Reserve Expire Time</md-table-head>
              <md-table-head>Status</md-table-head>
              <md-table-head>Actions</md-table-head>
            </md-table-row>
            <md-table-row v-for="ac in hookedRes" :key="ac">
              <md-table-cell>{{ ac.trailerid }}</md-table-cell>
              <md-table-cell>{{ ac.truckid }}</md-table-cell>
              <md-table-cell>{{ ac.city }}</md-table-cell>
              <md-table-cell>{{ ac.state }}</md-table-cell>
              <md-table-cell>{{ ac.reserved }}</md-table-cell>
              <md-table-cell>{{ ac.time }}</md-table-cell>
              <md-table-cell>{{ ac.status }}</md-table-cell>
              <md-table-cell style="width:15%">
              <md-button class="md-icon-button md-raised" @click="unhook(ac._id,ac.truckid,ac.trailerid)">
                <md-icon>remove_circle</md-icon>
              </md-button>
              </md-table-cell>
            </md-table-row>
          </md-table>
        </md-tab>
        <md-tab id="tab-history" md-label="History">
          <md-table>
            <md-table-row>
              <md-table-head>Trailer ID</md-table-head>
              <md-table-head>Truck ID</md-table-head>
              <md-table-head>City</md-table-head>
              <md-table-head>State</md-table-head>
              <md-table-head>Reserved Time</md-table-head>
              <md-table-head>Reserve Expire Time</md-table-head>
              <md-table-head>Status</md-table-head>
            </md-table-row>
            <md-table-row v-for="ac in historyRes" :key="ac">
              <md-table-cell>{{ ac.trailerid }}</md-table-cell>
              <md-table-cell>{{ ac.truckid }}</md-table-cell>
              <md-table-cell>{{ ac.city }}</md-table-cell>
              <md-table-cell>{{ ac.state }}</md-table-cell>
              <md-table-cell>{{ ac.reserved }}</md-table-cell>
              <md-table-cell>{{ ac.time }}</md-table-cell>
              <md-table-cell>{{ ac.status }}</md-table-cell>
            </md-table-row>
          </md-table>
        </md-tab>
      </md-tabs>
      <md-snackbar :md-active.sync="snackbar">{{ msg }}</md-snackbar>
    </div>
</template>

<script>
import axios from 'axios'
import date from 'date-and-time'

export default {
  name: 'Reserves',
  data: () => ({
    url: 'http://ben-logistics-api.eu-4.evennode.com/graphql',
    // url: 'http://localhost:3000/graphql',
    activeRes: null,
    hookedRes: null,
    historyRes: null,
    loaded: true,
    snackbar: false,
    msg: ''
  }),
  methods: {
    tab (activeTab) {
      if (activeTab === 'tab-active') { this.loaded = true; this.activeDataFetch() }
      if (activeTab === 'tab-hooked') { this.loaded = true; this.hookedDataFetch() }
      if (activeTab === 'tab-history') { this.loaded = true; this.historyDataFetch() }
    },
    hookup (id, exp) {
      this.loaded = true
      axios.post(this.url, {
        query: `{ currentTime { Curr }}`
      }).then(res => {
        var date1 = new Date(res.data.data.currentTime.Curr)
        var date2 = new Date(exp)
        if (date.subtract(date2, date1).toMinutes() <= 0) {
          this.msg = 'Cannot hookup trailer! Reserve time expired!'
          this.snackbar = true
          this.loaded = false
        } else {
          axios.post(this.url, {
            query: `mutation{ hook(_id:"` + id + `"){ _id status }}`
          }).then(response => {
            this.activeDataFetch()
          })
        }
      })
    },
    unhook (id, truck, trailer) {
      this.loaded = true
      axios.post(this.url, {
        query: `mutation{ deliver(_id:"` + id + `",truck:` + truck + `,trailer:` + trailer + `){ _id status }}`
      }).then(response => {
        this.hookedDataFetch()
      })
    },
    release (id, trailerid, truckid, exp) {
      var date1
      this.loaded = true
      axios.post(this.url, {
        query: `{ currentTime { Curr }}`
      }).then(res => {
        date1 = new Date(res.data.data.currentTime.Curr)
        var date2 = new Date(exp)
        if (date.subtract(date2, date1).toMinutes() <= 0) {
          this.msg = 'Cannot hookup trailer! Reserve time expired!'
          this.snackbar = true
          this.loaded = false
        } else {
          axios.post(this.url, {
            query: `mutation { freeReserve(_id:"` + id + `",trailerid:` + trailerid + `, truckid: ` + truckid + `){ _id } }`
          }).catch(e => {
            console.log(e)
          })
          this.activeDataFetch()
        }
      })
    },
    activeDataFetch () {
      this.activeRes = null
      axios.post(this.url, {
        query: `{ userActiveReserves(user:"` + this.$session.get('creds').login + `"){ _id trailerid reserved lat long time truckid city state user status }}`
      }).then(response => {
        if (response.data.data.userActiveReserves.length > 0) {
          this.activeRes = response.data.data.userActiveReserves
        }
        this.loaded = false
      })
    },
    hookedDataFetch () {
      this.hookedRes = null
      axios.post(this.url, {
        query: `{ userHookedReserves(user:"` + this.$session.get('creds').login + `"){ _id trailerid reserved lat long time truckid city state user status }}`
      }).then(response => {
        if (response.data.data.userHookedReserves.length > 0) {
          this.hookedRes = response.data.data.userHookedReserves
        }
        this.loaded = false
      })
    },
    historyDataFetch () {
      this.historyRes = null
      axios.post(this.url, {
        query: `{ userDeliveredReserves(user:"` + this.$session.get('creds').login + `"){ _id trailerid reserved lat long time truckid city state user status }}`
      }).then(response => {
        if (response.data.data.userDeliveredReserves.length > 0) {
          this.historyRes = response.data.data.userDeliveredReserves
        }
        this.loaded = false
      })
    }
  },
  created () {
    this.activeDataFetch()
  }
}
</script>



