<template>
  <div>
    <form novalidate class="md-layout md-alignment-center-center" @submit.prevent="validateUser">
      <md-card class="md-layout-item md-size-50 md-small-size-100">
        <md-card-header>
          <div class="md-title">Change Password</div>
        </md-card-header>

        <md-card-content>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('oldPass')">
                <label for="first-name">Old Password</label>
                <md-input type="password" name="first-name" id="first-name" autocomplete="given-name" v-model="form.oldPass" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.oldPass.required">The Old Password is required</span>
                <span class="md-error" v-else-if="!$v.form.oldPass.minlength">Invalid Old Password</span>
              </md-field>
            </div>
          </div>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('newPass')">
                <label for="last-name">New Password</label>
                <md-input type="password" name="last-name" id="last-name" autocomplete="family-name" v-model="form.newPass" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.newPass.required">The New Password is required</span>
                <span class="md-error" v-else-if="!$v.form.newPass.minlength">Invalid New Password</span>
              </md-field>
            </div>
          </div>
          <div class="md-layout md-gutter">
            <div class="md-layout-item md-small-size-100">
              <md-field :class="getValidationClass('conPass')">
                <label for="last-name">Confirm Password</label>
                <md-input type="password" name="last-name" id="last-name" autocomplete="family-name" v-model="form.conPass" :disabled="sending" />
                <span class="md-error" v-if="!$v.form.conPass.required">The Confirm Password is required</span>
                <span class="md-error" v-else-if="!$v.form.conPass.minlength">Invalid Confirm Password</span>
              </md-field>
            </div>
          </div>
        </md-card-content>

        <md-progress-bar md-mode="indeterminate" v-if="sending" />

        <md-card-actions>
          <md-button type="submit" class="md-primary" :disabled="sending">Create user</md-button>
        </md-card-actions>
      </md-card>

      <md-snackbar :md-active.sync="snackbar">{{ msg }}</md-snackbar>
    </form>
  </div>
</template>

<script>
import axios from 'axios'
import { validationMixin } from 'vuelidate'
import {
  required,
  minLength,
  maxLength
} from 'vuelidate/lib/validators'
export default {
  name: 'FormValidation',
  mixins: [validationMixin],
  data: () => ({
    url: 'http://ben-logistics-api.eu-4.evennode.com/graphql',
    form: {
      oldPass: null,
      newPass: null,
      gender: null,
      age: null,
      email: null
    },
    snackbar: false,
    sending: false,
    msg: ''
  }),
  validations: {
    form: {
      oldPass: {
        required,
        minLength: minLength(6)
      },
      newPass: {
        required,
        minLength: minLength(6)
      },
      conPass: {
        required,
        minLength: maxLength(6)
      }
    }
  },
  methods: {
    getValidationClass (fieldName) {
      const field = this.$v.form[fieldName]
      if (field) {
        return {
          'md-invalid': field.$invalid && field.$dirty
        }
      }
    },
    clearForm () {
      this.$v.$reset()
      this.form.oldPass = null
      this.form.newPass = null
      this.form.conPass = null
    },
    saveUser () {
      this.sending = true
      axios.post(this.url, {
        query: `mutation { changePassword(login:"` + this.$session.get('creds').login + `",oldPass:"` + this.form.oldPass + `", newPass: "` + this.form.newPass + `", conPass: "` + this.form.conPass + `"){ login } }`
      }).catch(e => {
        console.log(e)
      })
      this.sending = false
      this.msg = 'Password changed! Please relogin with new password'
      this.snackbar = true
    },
    validateUser () {
      if (this.form.newPass !== this.form.conPass) {
        this.msg = 'Passwords does not match!'
        this.snackbar = true
      } else {
        this.$v.$touch()
        if (!this.$v.$invalid) {
          this.saveUser()
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .md-progress-bar {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
  }
</style>



