<template>
  <div class="page-container md-layout-column" style="height:100vh !important min-height:100%">
    <md-toolbar class="md-primary">
      <md-button class="md-icon-button" @click="showNavigation = true">
        <md-icon>menu</md-icon>
      </md-button>
      <span class="md-title">My Title</span><md-icon v-bind:style="pingSt">swap_vert</md-icon>
      <div class="md-toolbar-section-end">
        <md-menu md-size="auto" md-direction="bottom-end" :md-active.sync="toggleCard" md-align-trigger>
        <md-badge v-bind:md-content="nots.length">
          <md-button class="md-icon-button" md-menu-trigger>
            <md-icon>notifications</md-icon>
          </md-button>
        </md-badge>

        <md-menu-content>
          <md-menu-item to="/reserves" @click="notif()" v-for="not in nots" :key="not" ><span class="md-caption">{{not.title}}</span></md-menu-item>
        </md-menu-content>
        </md-menu>
        <md-button @click="showSidepanel = true" class="md-icon-button md-dense md-accent">
          <md-icon class="md-size-2x">account_circle</md-icon>
        </md-button>
      </div>
    </md-toolbar>
      <md-drawer :md-active.sync="showNavigation">
      <md-toolbar class="md-transparent" md-elevation="0">
        <span class="md-title">My App name</span>
      </md-toolbar>

      <md-list>
        <md-list-item to="/page">
          <md-icon>dashboard</md-icon>
          <span class="md-list-item-text">Dashboard</span>
        </md-list-item>

        <md-list-item to="/reserves">
          <md-icon>local_shipping</md-icon>
          <span class="md-list-item-text">My Reserves</span>
        </md-list-item>

      </md-list>
    </md-drawer>
        <md-drawer class="md-right" :md-active.sync="showSidepanel">
      <md-card>
      <md-card-media>
        <md-icon class="md-size-2x">account_circle</md-icon>
      </md-card-media>

      <md-card-header>
        <div class="md-title">{{ first_name + " " + last_name }}</div>
        <div class="md-subhead">{{ "@" + username }}</div>
      </md-card-header>
      <md-card-area>
      <md-list>
        <md-list-item to="/password">Change password</md-list-item>
        <md-list-item @click="logout">
          Exit
          <md-icon class="color:red">exit_to_app</md-icon>
        </md-list-item>
      </md-list>
      </md-card-area>
    </md-card>
    </md-drawer>
      <first v-if="page == '/page'"></first>
      <reserves v-if="page == '/reserves'"></reserves>
      <!-- <password v-if="page == '/password'"></password> -->
  </div>
</template>

<script>
import First from './views/First.vue'
import Reserves from './views/reserves.vue'
import Password from './views/password.vue'
import io from 'socket.io-client'
// import Pusher from 'pusher-js'
// import Password from './views/Password.vue'

// const socket = new Pusher('5db59e0050361fadf97d', {
//   cluster: 'ap2',
//   encrypted: true
// })
// const channel = socket.subscribe('qwerty')

export default {
  name: 'Temporary',
  data: () => ({
    first_name: '',
    last_name: '',
    username: '',
    pingSt: true,
    showNavigation: false,
    showSidepanel: false,
    page: '/page',
    nots: [],
    socket: io('http://ben-logistics-socket.eu-4.evennode.com/')
    // socket: io('http://localhost:3000')
  }),
  beforeCreate: function () {
    if (!this.$session.exists()) {
      this.$router.push('/')
    }
  },
  created: function () {
    let cred = this.$session.get('creds')
    this.first_name = cred.first_name
    this.last_name = cred.last_name
    this.username = cred.login
    this.socket.on('ping', data => {
      this.pingSt = 'color: green;'
    })
    this.socket.on('connect_error', data => {
      this.pingSt = 'color: red;'
    })
    this.socket.on(this.username, data => {
      this.nots.push({title: data.message})
    })
  },
  watch: {
    $route (to, from, next) {
      this.page = to.path
      this.showNavigation = false
      this.showSidepanel = false
    }
  },
  methods: {
    logout: function () {
      this.$session.destroy()
      this.socket.disconnect()
      this.$router.push('/')
    },
    notif: function () {
      this.nots = []
    }
  },
  components: {
    First,
    Reserves,
    Password
  }
}
</script>

<style lang="scss" scoped>
  .page-container {
    min-height: 300px;
    overflow: hidden;
    position: relative;
    border: 1px solid rgba(#000, .12);
  }

   // Demo purposes only
  .md-drawer {
    width: 230px;
    max-width: calc(100vw - 125px);
  }

  .md-content {
    padding: 16px;
  }
  .md-list {
    width: 320px;
    max-width: 100%;
    display: inline-block;
    vertical-align: top;
    border: 1px solid rgba(#000, .12);
  }
</style>



